import React from "react";

import Button from "@material-ui/core/Button";

function DrawerContent(props) {
    const { openControl } = props;
    const handleBtnClick = e => {
        openControl(e.currentTarget.value);
    };

    return (
        <>
            <Button variant="contained" value="model" onClick={handleBtnClick}>
                Choose a model
            </Button>
            <Button
                variant="contained"
                value="animation"
                onClick={handleBtnClick}
            >
                Choose Animation
            </Button>
            <Button
                variant="contained"
                value="background"
                onClick={handleBtnClick}
            >
                Background Settings
            </Button>
        </>
    );
}

export default DrawerContent;
