import React, { useState, useRef, useEffect } from "react";
import useToggleState from "./hooks/useToggleState";
import { Divider } from "@material-ui/core";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import useStyles from "./styles/MainPageStyles";

import Display from "./Display";
import DrawerContent from "./DrawerContent";
import ControlPanel from "./ControlPanel";
import { SettingsProvider } from "./context/SettingsContext";

function MainPage(props) {
    const classes = useStyles();

    const [currentMode, setCurrentMode] = useState("model");
    const [drawerOpen, toggleDrawerOpen] = useToggleState(true);
    const [controlOpen, toggleControlOpen] = useToggleState(false);

    const viewerRef = useRef();
    const [viewport, setViewport] = useState({ width: 500, height: 500 });

    useEffect(() => {
        updateViewportSize();
        window.addEventListener("resize", updateViewportSize);
        return function () {
            window.removeEventListener("resize", updateViewportSize);
        };
    }, []);

    const updateViewportSize = () => {
        const height = viewerRef.current.clientHeight;
        const width = viewerRef.current.clientWidth;
        setViewport({ width, height: height - 5 });
    };

    const openControl = mode => {
        toggleControlOpen();
        setCurrentMode(mode);
    };

    return (
        <SettingsProvider>
            <div className={classes.root}>
                <CssBaseline />
                <ControlPanel
                    mode={currentMode}
                    open={controlOpen}
                    toggleControlOpen={toggleControlOpen}
                />
                <button
                    onClick={toggleDrawerOpen}
                    className={classes.openDrawerButton}
                >
                    Settings
                </button>
                <Drawer
                    className={classes.drawer}
                    variant="persistent"
                    anchor="left"
                    open={drawerOpen}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                >
                    <div className={classes.drawerHeader}>
                        <span>Settings</span>
                    </div>
                    <div
                        className={classes.closeDrawerButton}
                        onClick={toggleDrawerOpen}
                    >
                        {"<"}
                    </div>
                    <Divider />
                    <DrawerContent openControl={openControl} />
                </Drawer>
                <main ref={viewerRef} className={classes.content}>
                    <Display viewport={viewport} />
                </main>
            </div>
        </SettingsProvider>
    );
}

export default MainPage;
