import React, { useState, useContext } from "react";
import { DialogContent, DialogTitle, DialogTop } from "./CustomDialog";
import Button from "@material-ui/core/Button";

import { DispatchContext } from "./context/SettingsContext";

function BackgroundSettings(props) {
    const { toggleControlOpen } = props;
    const dispatch = useContext(DispatchContext);

    const setGreenScreen = () => {
        const action = {
            type: "update",
            key: "scene",
            value: { background: 0x00ff00 },
        };
        dispatch(action);
        toggleControlOpen();
    };

    return (
        <>
            <DialogTop>
                <DialogTitle onClose={toggleControlOpen}>
                    Background Settings
                </DialogTitle>
            </DialogTop>
            <DialogContent dividers>
                <Button
                    onClick={setGreenScreen}
                    variant="outlined"
                    color="primary"
                >
                    Green Screen
                </Button>
            </DialogContent>
        </>
    );
}

export default BackgroundSettings;
