import React, { useContext } from "react";
import { DialogContent, DialogTitle, DialogTop } from "./CustomDialog";
import Button from "@material-ui/core/Button";

import { DispatchContext } from "./context/SettingsContext";

import animationList from "./data/animationList";

function AnimationSelect(props) {
    const { toggleControlOpen } = props;
    const dispatch = useContext(DispatchContext);
    const handleSelect = event => {
        event.persist();
        const aniName = event.currentTarget.dataset.value;
        const action = {
            type: "update",
            key: "animation",
            value: { name: aniName },
        };
        dispatch(action);
        toggleControlOpen();
    };

    const content = Object.keys(animationList).map(ani => (
        <Button
            variant="contained"
            key={ani}
            data-value={ani}
            onClick={handleSelect}
        >
            {ani}
        </Button>
    ));

    return (
        <>
            <DialogTop>
                <DialogTitle onClose={toggleControlOpen}>
                    Select an Animation
                </DialogTitle>
            </DialogTop>
            <DialogContent dividers>
                <div>{content}</div>
            </DialogContent>
        </>
    );
}

export default AnimationSelect;
