import React, { useContext } from "react";
import { SettingsContext } from "./context/SettingsContext";
import ModelViewer from "./ModelViewer";
import "./styles/Display.css";

import { fbxSource } from "./App";
import { faceOffsets, cameraPositions, controlsPositions } from "./consts";
import animationList from "./data/animationList";

function Display(props) {
    const { viewport } = props;
    const settings = useContext(SettingsContext);
    const {
        model: { id, face, texture },
        scene: { background: bgColor },
        animation: { name: aniName },
    } = settings;

    const animationIdx = animationList[aniName] || aniName;

    const modelPath = `${fbxSource}/fbx/${id}/${id}.fbx`;
    const texturePath = `${fbxSource}/fbx/${texture}/${texture}.png`;

    const faceOffset = faceOffsets[`face${face}`];

    const type = id[0];

    let cameraPosition = cameraPositions[type];
    if (cameraPositions[id]) {
        cameraPosition = cameraPositions[id];
    }

    let controlsPosition = controlsPositions[type];
    if (controlsPositions[id]) {
        controlsPosition = controlsPositions[id];
    }

    return (
        <div className="Display">
            <ModelViewer
                model={modelPath}
                texture={texturePath}
                viewport={viewport}
                faceOffset={faceOffset}
                cameraPosition={cameraPosition}
                controlsPosition={controlsPosition}
                animationIdx={animationIdx}
                bgColor={bgColor}
                rotation={{ x: 0, y: Math.PI / 4, z: 0 }}
            />
        </div>
    );
}

export default Display;
